<template>
  <div class="model-viewer">
    <div id="demo-viewer" :class="left?'left':''">
      <div id="demo" style>
        <helios-viewer v-if="component=='viewer'" background style="height: 90%; width: 100%;"></helios-viewer>
        <helios-background v-if="component=='background'" style="height: 90%; width: 100%;"></helios-background>

        <div style="margin: 1vmin"></div>
        <h3>
          Drag the corner to demo aspect ratios
          <i class="fas fa-level-up-alt"></i>
        </h3>
      </div>
      <div id="demo-info">
        <h1>{{title}}</h1>
        <h2>
          <code :id="codeId"></code>
        </h2>
        <div style="margin: 1vmin"></div>
        <hr />
        <div style="width: 80%; margin: auto;">
          <div style="margin: 5vmin"></div>
          <h2>A responsive web component for displaying {{what}}.</h2>

          <div style="margin: 6vmin"></div>
          <h2>{{des_1}}</h2>
          <div style="margin: 6vmin"></div>
          <h2>
            <button @click="left=true">More info</button>
          </h2>
        </div>
      </div>
      <div style="position: absolute; width: 100%; height: 100%; left: 101%;  top: 0;">
        <h2>
          <button @click="left=false">WIP - Back</button>
        </h2>
      </div>
    </div>
  </div>
</template>
<script>
import { v4 as uuidv4 } from "uuid";

export default {
  name: "ModelViewer",
  props: ["title", "component", "what", "des_1", "des_2"],
  data: function() {
    return {
      uuid: uuidv4(),
      left: false
    };
  },
  mounted() {
    let textNode;
    if (this.$props.component == "viewer") {
      textNode = document.createTextNode("<helios-viewer></helios-viewer>");
    } else if (this.$props.component == "background") {
      textNode = document.createTextNode(
        "<helios-background></helios-background>"
      );
    }
    document.getElementById(this.codeId).appendChild(textNode);
  },
  computed: {
    codeId: function() {
      return "viewer-code" + this.uuid;
    }
  }
};
</script>

